import HeroSection from "./landingPageComponents/HeroSection";
import AssetsLifeCycle from "./landingPageComponents/AssetsLifeCycle";
import BlackHouseProducts from "./landingPageComponents/BlackHouseProducts";
import Proof from "./landingPageComponents/Proof";
import AccessSection from "./landingPageComponents/AccessSection";
import Footer from "./landingPageComponents/Footer";
import { useEffect } from "react";
import { Box, useMediaQuery, useTheme } from "@mui/material";
import $ from 'jquery';
import './PagePilling/jquery.pagepiling';
import './PagePilling/jquery.pagepiling.css';
import './PagePilling/pagePillingCSS.css'

function LandingPage() {
  useEffect(() => {
    {
      window.screen.width >= 900 ? (
        $(document).ready(function () {
          $('#pagepiling').pagepiling({
            menu: null,
            direction: 'vertical',
            verticalCentered: true,
            sectionsColor: [],
            anchors: [],
            scrollingSpeed: 700,
            easing: 'swing',
            loopBottom: false,
            loopTop: false,
            css3: true,
            navigation: {
              'textColor': '#fff',
              'bulletsColor': '#fff',
              'position': 'right',
              'tooltips': ['Hero Section', 'Unlock Private Credit', 'Trading System', 'The Proof', 'Footer']
            },
            normalScrollElements: null,
            normalScrollElementTouchThreshold: 5,
            touchSensitivity: 5,
            keyboardScrolling: true,
            sectionSelector: '.section',
            animateAnchor: true,

            //events
            onLeave: function (index, nextIndex, direction) { },
            afterLoad: function (anchorLink, index) { },
            afterRender: function () { },
          })
        })
      ) : (console.log('hello'))
    }
  }, [])

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <>
      {isMobile ? (
        <Box sx={{ background: "#17181D" }}>
          <HeroSection />
          <AssetsLifeCycle />
          <BlackHouseProducts />
          <Proof />
          <AccessSection />
          <Footer />
        </Box>
      ) : (
        <Box sx={{ background: "#17181D" }}>
          <div id="pagepiling">
            <div className="section sec1"><HeroSection /></div>
            <div className="section sec2"><AssetsLifeCycle /></div>
            <div className="section sec3"><BlackHouseProducts /></div>
            <div className="section sec4"><Proof /></div>
            <div className="section sec5"><div style={{ backgroundColor: '#17181D', height: '100vh' }}>
              <AccessSection /><br /><br /><br /><br /><Footer />
            </div></div>
          </div>
        </Box>
      )}
    </>
  );
}

export default LandingPage;