import { CopyRightText, FooterContainer, IconBox } from "./CustomComponents";
import { Box } from "@mui/material";
import logo from "../../Images/landingPage/landing-page/BLOCKHOUSE.png";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import TwitterIcon from "@mui/icons-material/Twitter";
import LanguageIcon from "@mui/icons-material/Language";

function Footer() {
  return (
    <Box style={{ backgroundColor: "#17181D" }}>
      <FooterContainer className="footerCOntainer">
        <img style={{ width: "206px", height: "40px" }} src={logo} alt="logo" />
        <IconBox>
          <LinkedInIcon />
          <TwitterIcon />
          <LanguageIcon />
        </IconBox>
        {/* <CopyRightText>
        <span style={{ color: "rgba(255, 255, 255, 0.6)" }}>
          made by
        </span>{" "}
        roobinium.io
      </CopyRightText> */}
      </FooterContainer>
    </Box>
  );
}

export default Footer;
